import { Component } from "react";
import PropTypes from "prop-types";
import { withRouter } from "react-router-dom";
import { fetchMoviesCast } from "../../services/moviesApi";
import noImage from "../../images/noImage.jpg";
import styles from "../Cast/cast.module.css";

const imagesURL = "https://image.tmdb.org/t/p/w500";
class Cast extends Component {
  state = {
    casts: [],
  };
  async componentDidMount() {
    const movieId = this.props.match.params.movieId;
    fetchMoviesCast(movieId).then((cast) => this.setState({ casts: cast }));
  }
  render() {
    const { casts } = this.state;
    return (
      <div>
        <ul className={styles.castList}>
          {casts &&
            casts.map(({ id, name, character, profile_path }) => (
              <li key={id} className={styles.cardItem}>
                {profile_path ? (
                  <img src={`${imagesURL}${profile_path}`} alt={name} />
                ) : (
                  <img src={noImage} alt={name} />
                )}

                <p className={styles.name}>{name}</p>
                <p className={styles.character}>{character}</p>
              </li>
            ))}
        </ul>
      </div>
    );
  }
}
Cast.propTypes = {
  match: PropTypes.shape({
    params: PropTypes.shape({ movieId: PropTypes.string }),
    path: PropTypes.string,
  }),
};
export default withRouter(Cast);
